import interfaceurl from '@/utils/interface.js'
import navigationBar from '@/components/navigation-bar.vue' //引入自定义导航栏
export default {
	components: {
		navigationBar
	},
	data() {
		return {
			//设置导航栏样式
			navigationBarStyle: {
				iconText: 'VIP' //导航栏文字
			},
			rankList: [], //佣金排名
			userInfo: {}, //获取用户信息
			userDetail: {}, //获取用户详情
			userNumber: {} //获取用户的粉丝和会员人数
		}
	},
	onShow() {
		this.userInfo = uni.getStorageSync('userInfo')
		if(uni.getStorageSync('token')) {
			this.getUserDetail();
			this.getUserNumber();
			this.getRanking()
		}
	},
	methods: {
		//获取用户详情
		getUserDetail() {
			let that = this
			interfaceurl.checkAuth(interfaceurl.showDetail, {}).then((res) => {
				that.userDetail = res.data
			});
		},
		//获取用户的粉丝和会员人数
		getUserNumber() {
			let that = this
			interfaceurl.checkAuth(interfaceurl.surveyList, {}).then((res) => {
				that.userNumber = res.data
			});
		},
		//获取排名列表
		getRanking() {
			let that = this
			interfaceurl.checkAuth(interfaceurl.teamRankingList, {}).then((res) => {
				that.rankList = res.data
			});
		}
	}
}
